anas que se desarrollan dentro del ámbito del proyecto, incluyendo las actividades sociales, económicas y culturales. Los aspectos a considerar en esta categoría son el tamaño, distribución y características de la población humana, así como las actividades económicas (empleo, inversión, ingreso promedio, impuestos, etc.) en el área del proyecto. ")]),_c('h5',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Es importante describir: ")]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"row justify-content-between mx-0 bg11 mb-3",attrs:{"data-aos":"flip-down"}},[_c('div',{staticClass:"col-auto p-0"},[_c('img',{attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/19.svg"),"alt":""}})]),_c('div',{staticClass:"col"},[_c('div',{staticClass:"p-3 my-4 me-2"},[_c('h4',{attrs:{"data-aos":"fade-down"}},[_vm._v("Infraestructura socioeconómica ")]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Servicios e instalaciones públicas y privadas, el transporte, las viviendas, las escuelas, los parques, la protección policial y contra incendios, los servicios de salud, el abastecimiento de agua y desagüe, el manejo del agua de lluvia y los desechos sólidos. ")])])])]),_c('div',{staticClass:"row justify-content-between mx-0 bg11 mb-3",attrs:{"data-aos":"flip-down"}},[_c('div',{staticClass:"col-auto p-0"},[_c('img',{attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/19.svg"),"alt":""}})]),_c('div',{staticClass:"col"},[_c('div',{staticClass:"p-3 my-4 me-2"},[_c('h4',{attrs:{"data-aos":"fade-down"}},[_vm._v("El tráfico ")]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("El proyecto podría tener impacto en el corto o largo plazo sobre las vías de acceso o los niveles de tráfico, así como los recursos energéticos y los materiales peligrosos. ")])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c|